@import 'swiper/swiper-bundle.min.css';
.page .swiper .swiper-wrapper .swiper-slide {
  cursor: grab;
}

[class*=devicon-] + [class*=devicon-] {
  margin-left: 0.5rem;
}

.tooltip {
  --bs-tooltip-border-radius: 1.5rem;
  --bs-tooltip-margin: 1rem;
  --bs-tooltip-padding-x: 1.5rem;
  --bs-tooltip-padding-y: 0.575rem;
  --bs-tooltip-font-size: 1.1rem;
}

.switch {
  display: inline-block;
  width: 64px;
  height: 32px;
  cursor: pointer;
  position: relative;
  background-color: rgba(0, 0, 0, 0.3) !important;
  border: 1px solid var(--bs-border-color);
}
.switch .slider {
  background: var(--bs-body-bg);
  width: 36px;
  height: 36px;
  position: absolute;
  top: -3px;
  left: -8px;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 1px solid var(--bs-border-color);
  transition: left 0.4s ease-in-out;
}
.switch.round {
  border-radius: 32px;
}
.switch.round .slider {
  border-radius: 50%;
}

.switch-input {
  opacity: 0;
  width: 0;
  height: 0;
  appearance: none;
}
.switch-input:checked ~ .switch {
  background: #0d6efd;
}
.switch-input:checked ~ .switch .slider {
  left: 36px;
}

:root {
  --animate-duration: 1s;
  --animate-delay: 1s;
  --animate-repeat: 1;
}

@keyframes rubberBand {
  from {
    transform: scale3d(1, 1, 1);
  }
  30% {
    transform: scale3d(1.25, 0.75, 1);
  }
  40% {
    transform: scale3d(0.75, 1.25, 1);
  }
  50% {
    transform: scale3d(1.15, 0.85, 1);
  }
  65% {
    transform: scale3d(0.95, 1.05, 1);
  }
  75% {
    transform: scale3d(1.05, 0.95, 1);
  }
  to {
    transform: scale3d(1, 1, 1);
  }
}
.blast:hover {
  animation-name: rubberBand;
  animation-duration: 1s;
  animation-fill-mode: both;
}

@keyframes bounceIn {
  from, 20%, 40%, 60%, 80%, to {
    animation-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
  }
  0% {
    opacity: 0;
    transform: scale3d(0.3, 0.3, 0.3);
  }
  20% {
    transform: scale3d(1.1, 1.1, 1.1);
  }
  40% {
    transform: scale3d(0.9, 0.9, 0.9);
  }
  60% {
    opacity: 1;
    transform: scale3d(1.03, 1.03, 1.03);
  }
  80% {
    transform: scale3d(0.97, 0.97, 0.97);
  }
  to {
    opacity: 1;
    transform: scale3d(1, 1, 1);
  }
}
#skills .list-skills [data-tags] {
  animation-name: bounceIn;
  animation-duration: 1s;
  animation-fill-mode: both;
  animation-duration: calc(var(--animate-duration) * 0.75);
}

:root {
  --bs-body-font-family: Poppins, var(--bs-font-sans-serif), sans-serif;
}

html {
  font-size: 16px;
  line-height: 1.2;
  scroll-behavior: smooth;
}

body {
  min-height: 100vh;
  width: 100vw;
  margin: 0;
  user-select: none;
  overflow-x: hidden;
}

*,
::before,
::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

a:not([class*=link-underline]) {
  text-decoration: none;
}
a:not([class*=link-underline]).active {
  pointer-events: none !important;
}

.page {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.page > .container {
  min-height: 60vh;
  display: flex;
  flex-direction: column;
}
.page > .container .heading {
  min-height: 20vh;
  margin-top: 72px;
}

.active {
  pointer-events: none;
}

.nav-pills {
  position: fixed;
  right: 1vw;
  z-index: 10;
  top: 50vh;
  transform: translateY(-50%);
  display: none;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
}
.nav-pills .pill {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 4px 0;
  padding: 6px;
}
.nav-pills .pill::after {
  content: "";
  display: inline-block;
  outline: 0;
  border: 2px solid rgba(123, 123, 123, 0.77);
  border-radius: 50%;
  padding: 6px;
  transition: border 0.6s ease-in-out;
}
.nav-pills .pill.active::after {
  border-color: rgba(202, 63, 63, 0.94);
  padding: 8px;
}
@media (min-width: 768px) {
  .nav-pills {
    display: flex;
  }
}

/* Logo */
.navbar-brand svg {
  min-width: 32px;
  min-height: 32px;
  max-width: 40px;
  max-height: 40px;
}

#logo svg {
  transition: transform 2s ease-in-out;
  transform-origin: center;
}
#logo:hover svg {
  transform: rotate(-1440deg);
}

/* Social media icons */
.social-media-link svg {
  max-width: 24px;
  max-height: 24px;
}

/* nav links */
.navbar .nav-link {
  position: relative;
  font-size: 1.3rem;
}
.navbar .nav-link:hover {
  font-weight: 700;
}
@media (min-width: 992px) {
  .navbar .nav-link {
    font-size: 1rem;
  }
  .navbar .nav-link::after {
    content: "";
    position: absolute;
    bottom: 0.125rem;
    background-color: rgb(64, 64, 64);
    border-radius: 50%;
    height: 0.125rem;
    left: 50%;
    right: 50%;
    transition: left 0.5s ease-in-out, right 0.5s ease-in-out;
  }
  .navbar .nav-link:hover::after {
    left: 5%;
    right: 5%;
  }
}

.navbar .navbar-brand,
.navbar .nav-link,
.social-media-link svg {
  color: rgba(var(--bs-dark-rgb), 0.8);
}
.navbar .navbar-brand:hover,
.navbar .nav-link:hover,
.social-media-link svg:hover {
  color: rgba(var(--bs-dark-rgb), 1);
}
[data-bs-theme=dark] .navbar .navbar-brand,
[data-bs-theme=dark] .navbar .nav-link,
[data-bs-theme=dark] .social-media-link svg {
  color: rgba(var(--bs-light-rgb), 0.8);
}
[data-bs-theme=dark] .navbar .navbar-brand:hover,
[data-bs-theme=dark] .navbar .nav-link:hover,
[data-bs-theme=dark] .social-media-link svg:hover {
  color: rgba(var(--bs-light-rgb), 1);
}

.nav-link.active {
  color: var(--bs-navbar-active-color) !important;
  font-weight: 700;
}

/* Hamburger */
.hamburger {
  width: 40px;
  position: absolute;
  height: 3px;
  right: 0;
  transition: transform 0.5s ease-in-out;
}
.hamburger::before, .hamburger::after {
  position: absolute;
  content: "";
  height: 3px;
  width: 40px;
  top: -0.75rem;
  right: 0;
  transition: transform 0.5s ease-in-out;
}
.hamburger::after {
  top: 0.75rem;
  transition: width 0.25s ease-in-out;
}

.navbar-toggler {
  box-shadow: none !important;
  border: 0 !important;
  position: fixed;
  z-index: 1050;
  right: 12px;
  top: 12px;
  height: 40px;
  width: 40px;
  padding: 12px;
  background: transparent;
}
.navbar-toggler[aria-expanded=true] .hamburger {
  transition: transform 0.5s ease-in-out;
  transform: rotate(45deg);
}
.navbar-toggler[aria-expanded=true] .hamburger::after {
  width: 0;
}
.navbar-toggler[aria-expanded=true] .hamburger::before {
  transform: translateY(0.75rem) rotate(90deg);
  transition: transform 0.5s ease-in-out;
}
.navbar-toggler .hamburger,
.navbar-toggler .hamburger::before,
.navbar-toggler .hamburger::after {
  background: rgba(var(--bs-dark-rgb), 0.8);
}
[data-bs-theme=dark] .navbar-toggler .hamburger,
[data-bs-theme=dark] .navbar-toggler .hamburger::before,
[data-bs-theme=dark] .navbar-toggler .hamburger::after {
  background: rgba(var(--bs-light-rgb), 0.8);
}
[data-view="#home"]:not(.navbar-shown, .navbar-collapsing) .navbar-toggler .hamburger,
[data-view="#home"]:not(.navbar-shown, .navbar-collapsing) .navbar-toggler .hamburger::before,
[data-view="#home"]:not(.navbar-shown, .navbar-collapsing) .navbar-toggler .hamburger::after {
  background: rgba(var(--bs-light-rgb), 0.8);
}
.navbar-toggler:hover .hamburger,
.navbar-toggler:hover .hamburger::before,
.navbar-toggler:hover .hamburger::after {
  background: rgba(var(--bs-dark-rgb), 1);
}
[data-bs-theme=dark] .navbar-toggler:hover .hamburger,
[data-bs-theme=dark] .navbar-toggler:hover .hamburger::before,
[data-bs-theme=dark] .navbar-toggler:hover .hamburger::after {
  background: rgba(var(--bs-light-rgb), 1);
}
[data-view="#home"]:not(.navbar-shown, .navbar-collapsing) .navbar-toggler:hover .hamburger,
[data-view="#home"]:not(.navbar-shown, .navbar-collapsing) .navbar-toggler:hover .hamburger::before,
[data-view="#home"]:not(.navbar-shown, .navbar-collapsing) .navbar-toggler:hover .hamburger::after {
  background: rgba(var(--bs-light-rgb), 1);
}

.switch {
  background: rgba(0, 0, 0, 0.3) !important;
  transform: scale(0.9);
}

#darkmode-slider svg[data-mode=dark] {
  display: none;
}

header {
  position: fixed;
  top: 0;
  right: 0;
  width: 0;
  z-index: 1030;
  min-height: 64px;
  background: rgba(var(--bs-light-rgb), 0.8);
  backdrop-filter: blur(3px);
  transition: width 0.5s ease-in-out;
}
[data-bs-theme=dark] header {
  background: rgba(var(--bs-dark-rgb), 0.8);
}

@media (min-width: 992px) {
  header {
    top: calc(100vh - 64px);
    width: 100vw;
    transition: top 0.5s ease-in-out;
  }
  body:not([data-view="#home"]) header {
    top: 0;
  }
}
.navbar-shown header,
.navbar-collapsing header {
  min-height: 100vh;
  width: 100vw;
  top: 0;
}
@media (min-width: 992px) {
  .navbar-shown header,
  .navbar-collapsing header {
    min-height: 64px;
  }
}

.github-icon {
  fill: #181616;
}
[data-bs-theme=dark] .github-icon {
  fill: #e7e9e9;
}

/* Landing Page */
.blast:hover {
  display: inline-block;
}

#home {
  min-height: calc(100vh - 1px);
  max-height: 100vh;
  color: rgb(64, 64, 64);
  position: relative;
  background: #161616;
}
#home h1,
#home h2 {
  background-image: linear-gradient(-225deg, #02d7f2 0%, #f2e900 29%, #007aff 67%, #ff1111 100%);
  background-clip: text;
  background-size: 200% auto;
  color: #fff;
  -webkit-text-fill-color: transparent;
  animation: tclip 5s linear infinite;
  text-align: center;
}
@media (min-width: 992px) {
  #home h1,
  #home h2 {
    text-align: left;
  }
}
@media (max-width: 768px) {
  #home h1 {
    font-size: 1.375rem;
  }
  #home h2 {
    font-size: 1.235rem;
  }
}

@keyframes tclip {
  to {
    background-position: 200% center;
  }
}
@keyframes scrollDownButton {
  0% {
    transform: translate(0, 0);
    opacity: 0;
  }
  40% {
    opacity: 1;
  }
  80% {
    transform: translate(0, 20px);
    opacity: 0;
  }
  100% {
    opacity: 0;
  }
}
.scroll-down-button {
  position: absolute;
  padding-top: 60px;
  bottom: 48px;
  left: 50vw;
}
.scroll-down-button span {
  position: absolute;
  top: 0;
  left: 50%;
  width: 30px;
  height: 50px;
  margin-left: -15px;
  border: 2px solid #fff;
  border-radius: 50px;
  box-sizing: border-box;
}
.scroll-down-button span:before {
  position: absolute;
  top: 10px;
  left: 50%;
  content: "";
  width: 6px;
  height: 6px;
  margin-left: -3px;
  background-color: #fff;
  border-radius: 100%;
  animation: scrollDownButton 2s infinite;
  box-sizing: border-box;
}
@media (min-width: 992px) {
  .scroll-down-button {
    bottom: 92px;
  }
}

.hiro {
  max-height: 70vh;
}
@media (min-width: 992px) {
  .hiro {
    max-height: 90vh;
    height: 100%;
  }
}
.hiro img {
  object-position: center center;
  height: 100%;
  max-width: 100%;
  object-fit: contain;
}

#about .photo {
  object-fit: cover;
  object-position: center center;
  width: 100%;
}
@media (max-width: 992px) {
  #about p {
    max-width: 400px;
    margin-left: auto;
    margin-right: auto;
  }
}
#about #about-me {
