}

@media (orientation: landscape) and (max-height: 450px) {
  html {
    overflow: hidden;
  }
  .landscape {
    display: flex;
    position: fixed;
    z-index: 5000;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    justify-content: center;
    align-items: center;
  }
  .landscape p {
    max-width: 66vw;
    text-align: center;
  }
}

